@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


/* #000000 */


.blog{
    
    height: 100%;
    /* background: yellow; */
    margin: 0%;
    position: fixed;
    bottom: 0;
    width: 100%;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}
.warning {
    background-color: #F2DEDE;
    z-index: -11065;
    font-size: 17px;
    border-radius: 10px;
    width: -webkit-fit-content;
    width: fit-content;
    width: -moz-fit-content;
    text-align: center;
    margin-left: auto;
    margin-right: auto;

}

.warning p {
    padding: 15px;
    color: #A94543;
    margin-left: 10px;
}

.warning p a {
    text-decoration: none;
    color: #A94543;
}

.warning p b {
    margin-right: 10px;
}

.home {
    width: 100%;
}

.home .mh {
    width: -webkit-fit-content;
    width: fit-content;
    width: -moz-fit-content;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    color: #000000;
    margin-bottom: 2%;

}

.home .mh h1 {
    font: bold 4.6em "Fira Sans", serif;
    /* -webkit-text-stroke: 3px #000000; */
    margin-bottom: -4%;
    margin-top: 50px;
    
}

.home .mh h4 {
    font: bold 1.1em "Fira Sans", serif;
    text-align: right;
    margin-right: 20px;
}

.home .mh a {
    color: #000000;
    text-decoration: none !important;
}

.button {
    border: 3px solid #000000;
    border-radius: 25px;
    cursor: pointer;
    padding: 10px 35px;
    text-align: center;
    text-decoration: none;
    font-size: 35px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    letter-spacing: 1.5px;
    background-color: Transparent;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;

    
}

.home .social {
    margin: 24px 0 !important;
    padding: 0;
    font-size: 30px;
    text-shadow: 0px 1px 2px #000000;
}

.home .social li {
    display: inline-block;
    margin: 0 15px;
    padding: 0;
}

.home .social a {
    color: black;
}

.home .social a:hover {
    color: #000000;
}
.quote{
    margin-top: 100px;
    width: -webkit-fit-content;
    width: fit-content;
    width: -moz-fit-content;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
}
.quote p{
    text-align: right;
}
.soc-panel{
    width: 100%;
    margin-top: 100px;
    text-align: center;
    padding: 5px;
    margin: 5px;
}
.link{
    text-decoration: none;
    cursor: pointer;

}
@media screen and (max-width: 450px){

    .warning p {
        padding: 10px 5px;
        color: #A94543;
        margin-left: 10px;
        font-size: .5em;
    }

    .home .mh h1{
    font: bold 50px "Fira Sans", serif;
    letter-spacing: 1.5px;
    margin-top: -2px;
    }

    .home .mh h4{
        text-align: center !important;

    }
    .home .button{
        font-size: 18px !important;
    }
    .quote{
        margin-top: 50px;
        font-size: 15px !important;
        width: -webkit-fit-content;
        width: fit-content;
        width: -moz-fit-content;
        text-align: center;
        margin-left: 5px;
        margin-right: 5px;
    }
    .quote p{
        text-align: center;
    }
    .social li{
        font-size: 15px !important;
    }
} 
@media screen and (max-width: 950px){

    .home .mh h1{
        font: bold 60 "Fira Sans", serif;
        letter-spacing: 1.5px;
    }
    .home .mh h4{
        text-align: center !important;

    }
    .home .button{
        font-size: 20px !important;
    }
    .quote{
        margin-top: 50px;
        width: -webkit-fit-content;
        width: fit-content;
        width: -moz-fit-content;
        text-align: center;
        margin-left: auto;
        margin-right: auto;
    }
    .quote p{
        text-align: center;
    }
    .social li{
        font-size: 17px;
    }
}

.topics-listing{
    margin-left: 10%;
    padding-top: 30px;
    margin-top: 2%;
    overflow: hidden;
}

/* Each item in the Feeds List */
.preview{
    width: 70%;
    padding: 25px;
    border: solid 1px #8B8B8B;
    background-image: -webkit-gradient(linear,left top, right top,from(#E6E6E6),to(#FFFFFF));
    background-image: -webkit-linear-gradient(left,#E6E6E6,#FFFFFF);
    background-image: linear-gradient(to right,#E6E6E6,#FFFFFF);
    margin-bottom: 50px;
}
.preview:hover{
    border: solid 3px #4B00D4;
}

/* Main heading of Each Item */
.preview b{
    font: bold 23px 'Lato', cursive;
}
.preview .abs{
    font: lighter 18px cursive;
}
.preview .date{
    color: #8c8c8c;
    font: medium 15px 'Roboto', cursive;

}
.header {
  background-color: #ffffff;
  border: 5px solid rgb(230, 230, 230);
  border-radius: 4px;
  text-align: center; 
  top: 0;
  left: 0;
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  overflow: hidden;
}
  
  /* Style the header links */
.header a {
  float: left;
  color: black;
  text-decoration: none;
}
  
  /* Style the logo link (notice that we set the same value of line-height and font-size to prevent the header to increase when the font gets bigger */
.header a.logo {
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 3px;
  padding-left: 3%;
}

.mh1 {
  width: -webkit-fit-content;
  width: fit-content;
  width: -moz-fit-content;
  text-align: left;
  color: rgb(85, 85, 85);
  font: bold 2em "Fira Sans", serif;
  -webkit-text-stroke: 2.5px rgb(0, 0, 0);
}

.power{
  float: right;
}

.power img {
  margin-right: 35px;
  border: 2px solid #4B00D4;
  border-radius: 50%;
  width: 50px;
}

@media screen and (max-width: 950px){
  .power{
    float: left;
    width: 15%;
    top: 0;
    left: 0;
    overflow-x: hidden;
    position: fixed;
    z-index: 1;
    margin-right: 3px;
  }
  .logo{
    width: 85%;
    float: right;
    right: 0;
  }
  .header a .mh1{
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    font-size: 30px;
  }


}
/* Footer */
.foot{
    color: black;
    text-align: center;
    bottom: 0 !important;
    left: 0;
    width: 100%;
    position: fixed;
    overflow: hidden;
    font-size: 15px;
    border: 1px solid #E6E6E6;
 }
 
 .foot a {
    text-decoration: none !important;
    color: black;
 }
 .foot a:hover{
    color: #11ABB0;
 }
 .foot a:active{
    color: #11ABB0;
 }
